
//News css style

.contain-news{
  height:150px;
  width:100%;
  padding-top:20px;
  text-align:center;
}
.news-title {
  width: 100%;
  margin-top:5px;

  p {
    font-size:30px;
    color: #DFD10F;
    padding-bottom:3px;
    display: inline-table;
    border-bottom: #DFD10F 1px solid;

  }
}

//.img-item{
//  width:100%;
//  position: relative;
//}
.img-item img{
  position:relative;
  width: 100%;
}
.news-img{
  height: 150px;
}
.box-shadow-on-img{
  box-shadow: 0 0 8px rgba(0,0,0,.3);
}
.overlay-img-box{
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(65,162,145,.4);
  opacity: 0;
  transition: opacity .3s ease;
  z-index: 100;
}
.img-item:hover .overlay-img-box  {
  opacity: 1;
}
.pcolor {
  color: gray;
  &:hover {
    color: black;
  }
}
.post-time{
  color: #B3B3B3 ;
  font-size: 14px;
}
@media  screen and (max-width: 1196px) {
  .news-and-event-background{
    width:50%;
    padding: 0;
    top:50%;
    left:25%;
  }
}
@media screen and (max-width: 518px) and (min-width: 360px) {
  .contain-news .news-title p {
    font-size: 24px;
  }
}
@media screen and (max-width: 351px){
  .news-and-event-background{
    font-size: 20px;
    height: 80px;
  }
  .contain-news .news-title p{
    font-size:18px;
  }
}

/* end style for News start style in contact us*/

.head-space {
  height: 450px;
  width: 100%;
  iframe {
    width: 100%;
    height: 450px;
  }
}
.background-gray{
  background-color: rgba(188, 184, 184, 0.15);
}
.make-center{
  text-align: center;
}
h3.styleh3{
  color: #ffcc00 ;
  font-size:36px;
  text-align: center;
}
h3.styleh3:after{

  content: "";
  display: block;
  height: 1px;
  background-color: #ffcc00 ;
  width: 130px;
  position: relative;
  margin: auto;
}
p.makecolor-email{
  color:#3294FC ;
}
h4.makesizeh4{
  font-size: 24px;
  font-weight: bold;
}
.makemargin{
  margin-top: 30px;
  margin-bottom: 20px;
}
.inputsetting{
  width:200px;
  color: gray;
}

.makemargin .form-control{
  border: none;
  background-color: transparent;
  border-bottom: 2px solid #ccc;
  padding-left: 0;
  min-height: 50px;
  box-shadow: none!important;
}
.make-center .btn-bigger {
  min-width: 180px;
  padding: 10px 20px;
}
.makeshadow-btn-send{
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.3);
}
.make-center .btn-primary {
  transition: background-color 0.4s ease;
  background-color: #f9c803 ;
  border-color: #f9c803 ;
  color: #505050 ;
  letter-spacing: 1px;
  border-radius: 0px;
  font-size: 18px;
}
.make-center button:hover{
  background-color:#fddb55 ;
  border-color: #fddb55 ;
}
/*end style for contact us page and start Quote Request page style*/
.quote-form-margin-top{
  margin-top:50px ;
}
.margin-right-to-weight{
  margin-right: 10px;
}
.form-control{
  border-radius: 0;
}
.font-weigth-label{
  font-weight: 400;
  color: gray;
}
.text_news{
  font-size: 18px;
  padding-top: 10px;
}
